export default {
  data () {
    return {
      pageShow: false,
      page: 0,
      isMore: false,
      list: [],
      isLoading: false
    }
  },

  onLoad () {
    this.getOrderList()
  },

  methods: {
    getOrderList () {
      if (this.isMore && this.isLoading) return

      this.isLoading = true
      this.$http.getDistributionOrderList({
        page: this.page + 1
      }).then(res => {
        if (res.status === 200) {
          this.isLoading = false
          if (!res.data.orderes.length) {
            this.isMore = true

            return false
          }

          if (this.list.length) {
            this.list = [...list, ...res.data.orderes]
          } else {
            this.list = res.data.orderes
          }
          this.pageShow = true
        } else {
          this.isLoading = false
          this.$toast(res.mess)
        }
      })
    }
  }
}
